.app__navbar {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background: rgba(235, 235, 235, 0.25);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border: 1px solid rgba(235, 235, 235, 0.25);
    position: fixed;
    z-index: 2;
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  
    img {
      width: auto;
      height: 30px;
  
      @media screen and (min-width: 2000px) {
        width: 180px;
        height: 40px;
      }
    }
  }
  
  .app__navbar-links {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    color: #55198b;
  
    li {
      margin: 0 1rem;
      cursor: pointer;
  
      flex-direction: column;
  
      div {
        width: 5px;
        height: 5px;
        background: transparent;
        border-radius: 50%;
  
        margin-bottom: 5px;
      }
  
      a {
        text-decoration: none;
        flex-direction: column;
  
        text-transform: uppercase;
        font-weight: 500;
  
        transition: all 0.3s ease-in-out;
  
        &:hover {
          color: #29c5d4;
        }
      }
    }
  
    @media screen and (max-width: 900px) {
      display: none;
    }
  }

  .app__navbar-menu {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #29c5d4;
    
    svg {
        width: 70%;
        height: 70%;
        color: var(--white-color);
      }
    
      div {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;
    
        padding: 1rem;
    
        width: 80%;
        height: 100vh;
        
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        
        
        background-color: var(--white-color);
        background-size: cover;
        background-repeat: repeat;
    
        /* top box shadow */
        box-shadow: 0px 0px 20px rgba(168, 168, 168, 0.15);
    
        svg {
          width: 35px;
          height: 35px;
          color: var(--secondary-color);
          margin: 0.5rem 1rem;
        }
    
        ul {
          list-style: none;
          margin: 0;
          padding: 0;
          height: 100%;
          width: 100%;
    
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          flex-direction: column;
    
          li {
            margin: 1rem;
    
            a {
              color: var(--gray-color);
              text-decoration: none;
              font-size: 1rem;
              text-transform: uppercase;
              font-weight: 500;
    
              transition: all 0.3s ease-in-out;
    
              &:hover {
                color: #29c5d4;
              }
            }
          }
        }
    
        @media screen and (min-width: 900px) {
          display: none;
        }
      }
    
      @media screen and (min-width: 900px) {
        display: none;
      }
  }